export default {
	data() {
		return {
			text: '',
			showMask: false,		//显示遮罩层
			nowIndex: '',			//显示转发的index
			showPosition: true,		//默认向下显示转发弹窗
			showTranspond: false, 	//转发弹窗
			showPopUp: false,		//底部弹窗
			showTextarea: false,	//评论编辑
			placeholder: '写评论…'
		};
	},
	// props 可以是数组或对象，用于接收来自父组件的数据
	props: ['blogData'],
	methods: {
		show(e,index) {	//显示转发

			uni.getSystemInfo({
			　　success: (res) => {
				
					const screenH = res.screenHeight,	// 屏幕的宽度
					domH = e.touches[0].clientY,		//元素位于顶部距离
					position = screenH/2 >= domH ? true : false
					
					this.showPosition = position
					this.showMask = true
					// 在小程序和app直接在子组件改变props的值,父组件的值不会变,导致状态没改变
					//需要直接在父组件修改值
					//只在h5界面不出现

						this.$parent._data.blog[index].showTranspond = true

					//只在h5界面出现



					this.showTranspond = true
					this.nowIndex = index	
			    }
			});
		},	
		show2(id){	//点击下拉图标显示弹窗
			this.showMask = true
			this.showPopUp = true
		},
		show3(id){	//点击下拉图标显示弹窗
			this.showMask = true
			this.showTextarea = true
		},
		hide() { //点击遮罩层隐藏
			if(this.showTranspond) {
				
				//只在h5界面不出现

					this.$parent._data.blog[this.nowIndex].showTranspond = false

				//只在h5界面出现



				this.showTranspond = false
				this.nowIndex = ''
			} else if(this.showPopUp) {
				this.showPopUp = false
			} else {
				this.showTextarea = false
			}
			this.showMask = false				
		},
		transpond(index){  //点击转发
			if(index == 1) {
				
			}else if(index == 2) {
				
			}else {
				
			}	
			//只在h5界面不出现

				this.$parent._data.blog[this.nowIndex].showTranspond = false

			//只在h5界面出现



			this.nowIndex = ''
			this.showTranspond = false
			this.showMask = false
		},
		like(index) {
			//只在h5界面不出现

				this.$parent._data.blog[index].Clike = !this.$parent._data.blog[index].Clike

			//只在h5界面出现



		},
		send(index) {
			if(this.text == '') {
				uni.showToast({
					title: "内容不能为空",
					icon: "none",
					duration: 2000
				})
			} else {
				uni.showToast({
					title: "评论成功",
					duration: 2000
				})
				this.showTextarea = false
				this.showMask = false
			}
		}
	}
}
